<template>
    <form @submit.prevent="onSubmit">
        <label for="new-todo-input-text">
            <span>What needs to be done?</span>
            <input type="text" id="new-todo-input-text" name="new-todo" ref="newToDoInputText" autocomplete="off" v-model.lazy.trim="label">
        </label>
        
        <label for="new-todo-input-check">
            <span>checked?</span>
            <input type="checkbox" id="new-todo-input-check" v-model="checked">
        </label>

        <button type="submit">Add</button>
    </form>
</template>

<script>
export default {
   methods: {
        onSubmit() {
            if (this.label === '') {
                return;
            }
            // console.log(this.label, this.checked)
            this.$emit('todo-added', this.index, this.label, this.checked);
            this.index++;
            this.label = '';
            this.checked = false;
            document.getElementById('new-todo-input-text').value = '';
            const newToDoInputText = this.$refs.newToDoInputText;
            newToDoInputText.focus();
        }
    },
    data() {
        return {
            index: 1,
            label: '',
            checked: false,
        }
    },
};
</script>

<style lang="scss" scoped>
form {
    label {
        display: block;
        text-align: left;
        margin-top: 20px;

        &:first-child {
            margin-top: 0;
        }

        span {
            display: inline-block;
            font-size: 18px;
            text-align: left;
            vertical-align: top;
        }
    }

    input {
        padding: 5px;
        margin: 0;
        box-sizing: border-box;
    }

    input[type="text"] {
        display: block;
        width: 100%;
        height: 35px;
        margin-top: 10px;
    }

    input[type="checkbox"] {
        width: 20px;
        height: 20px;
        margin-left: 10px;
    }

    button {
        margin-top: 20px;
    }
}
</style>