<template>
    <div id="container">
        <h1>To-Do List</h1>

        <to-do-form @todo-added="addToDo"></to-do-form>

        <div class="txtLength" ref="listSummary" tabindex="-1">
            <b>{{ToDoItems.filter(item => item.checked).length}}</b> out of <b>{{ToDoItems.length}}</b> items completed
        </div>

        <ul v-if="ToDoItems.length !== 0">
            <li v-for="item in ToDoItems" :key="item.id" :data-index="item.index">
                <to-do-item :label="item.label" :id="item.id" :checked="item.checked" @checkbox-changed="updateCheckedStatus(item.id)" @item-deleted="deleteToDo(item.id)" @item-edited="editToDo(item.id, $event)"></to-do-item>
            </li>
        </ul>
    </div>
</template>

<script>
import uniqueId from 'lodash.uniqueid';
import ToDoItem from './components/ToDoItem';
import ToDoForm from './components/ToDoForm';

export default {
    name: 'app',

    components: {
        ToDoItem,
        ToDoForm,
    },

    data() {
        return {
            ToDoItems: [
                // {id: uniqueId('todo-'), label: 'Learn Vue', checked: false},
                // {id: uniqueId('todo-'), label: 'Create a Vue project with the CLI', checked: true},
                // {id: uniqueId('todo-'), label: 'Have fun', checked: true},
                // {id: uniqueId('todo-'), label: 'Create a to-do list', checked: false},
            ],
        }
    },

    methods: {
        addToDo(toDoIndex, toDoLabel, toDoChecked) {
            // console.log('To-do Added:' + toDoLabel, 'To-do Checked:' + toDoChecked);
            this.ToDoItems.unshift({index: toDoIndex, id: uniqueId('todo-'), label: toDoLabel, checked: toDoChecked});
        },

        updateCheckedStatus(toDoId) {
            const toDoToUpdate = this.ToDoItems.find(item => item.id === toDoId);
            toDoToUpdate.checked = !toDoToUpdate.checked;
        },

        deleteToDo(toDoId) {
            const itemIndex = this.ToDoItems.findIndex(item => item.id === toDoId);
            this.ToDoItems.splice(itemIndex, 1);
            this.$refs.listSummary.focus();
        },

        editToDo(toDoId, newLabel) {
            const toDoToEdit = this.ToDoItems.find(item => item.id === toDoId);
            toDoToEdit.label = newLabel;
        }
    }
}
</script>

<style lang="scss">
body {
    margin: 0;
}

#app {
    max-height: 100vh;
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    padding: 60px 0;
}

#container {
    display: inline-block;
    min-width: 300px;
    padding: 50px 50px 0;
    border: 1px solid #333;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 0 5px;
    overflow: hidden;
}

h1 {
    line-height: 36px;
}

button {
    display: inline-block;
    width: 100%;
    height: 50px;
    font-weight: 700;
    font-size: 16px;
    color: #fff;
    padding: 0;
    border: none;
    background: #333;
}

ul {
    display: block;
    max-height: calc(100vh - 374px - 60px - 60px);
    padding: 0;
    margin: 0 -50px;
    border-top: 1px dashed #333;
    overflow: auto;
}

// border-top: 1px dashed #333;

li {
    list-style: none;
    padding: 30px 50px;
    border-top: 1px dashed #333;

    &:first-child {
        border-top: none;
    }

    &:nth-child(even) {        
        background-color: #efefef;
    }

    // &:before {
    //     content: attr(data-index)'.';
    //     display: inline-block;
    //     margin-right: 10px;
    // }
}

button {
    cursor: pointer;
}
</style>

<style lang="scss" scoped>
.txtLength {
    margin: 20px 0;
}
</style>