<template>
    <div v-if="!isEditing">
        <span>
            <label :for="id">
                {{label}}
                <input type="checkbox" :id="id" :checked="isChecked" @change="$emit('checkbox-changed')">
            </label>
        </span>

        <div class="btn-group">
            <button type="button" ref="editButton" @click="toggleToItemEditForm">
                Edit
                <!-- <span>{{label}}</span> -->
            </button>

            <button type="submit" @click="deleteToDo">
                Delete
                <!-- <span>{{label}}</span> -->
            </button>
        </div>
    </div>

    <to-do-item-edit-form v-else :id="id" :label="label" @item-edited="itemEdited" @edit-cancelled="editCancelled"></to-do-item-edit-form>
</template>

<script>
import ToDoItemEditForm from './ToDoItemEditForm';

export default {
    components: {
        ToDoItemEditForm
    },

    props: {
        label: {required: true, type: String},
        checked: {default: false, type: Boolean},
        id: {required: true, type: String},
    },

    data() {
        return {
            // isChecked: this.checked,
            isEditing: false,
        }
    },

    computed: {
        isChecked() {
            return this.checked;
        }
    },

    methods: {
        deleteToDo() {
            this.$emit('item-deleted');
        },

        toggleToItemEditForm() {
            this.isEditing = true;
        },

        itemEdited(newLabel) {
            this.$emit('item-edited', newLabel);
            this.isEditing = false;
            this.focusOnEditButton();
        },

        editCancelled() {
            this.isEditing = false;
            this.focusOnEditButton();
        },

        focusOnEditButton() {
            this.$nextTick(() => {
                const editButtonRef = this.$refs.editButton;
                editButtonRef.focus();
            });
        },
    },
    
};
</script>

<style lang="scss" scoped>
.btn-group {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;

    button {
        width: calc((100% - 5px) / 2);
        height: 40px;
        padding: 10px;

        &:last-child {
            color: #333;
            border: 1px solid #333;
            background-color: #fff;
        }

        span {
            font-size: 12px;
        }
    }
}
</style>