<template>
    <form @submit.prevent="onSubmit">
        <div>
            <label :for="id">
                <input type="text" :id="id" ref="labelInput" autocomplete="off" v-model.lazy.trim="newLabel">
            </label>
        </div>

        <div class="btn-group">
            <button type="button" class="btn" @click="onCancel">
                Cancel
            </button>

            <button type="submit" class="btn">
                Save
                <!-- <span>edit for {{label}}</span> -->
            </button>
        </div>
    </form>
</template>

<script>
export default {
    props: {
        label: {
            type: String,
            required: true,
        },

        id: {
            type: String,
            required: true,
        },
    },

    data() {
        return {
            newLabel: this.label,
        };
    },

    methods: {
        onSubmit() {
            if (this.newLabel && this.newLabel !== this.label) {
                this.$emit('item-edited', this.newLabel);
            }
        },

        onCancel() {
            this.$emit('edit-cancelled');
        },
    },

     mounted() {
        const labelInputRef = this.$refs.labelInput;
        labelInputRef.focus();
    }
};
</script>

<style lang="scss" scoped>
form {
    input[type="text"] {
        display: block;
        width: 100%;
        height: 35px;
        margin-top: 10px;
        padding: 5px;
        box-sizing: border-box;
    }
}
.btn-group {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;

    button {
        width: calc((100% - 5px) / 2);
        height: 40px;
        padding: 10px;

        &:first-child {
            color: #333;
            border: 1px solid #333;
            background-color: #fff;
        }

        span {
            font-size: 12px;
        }
    }
}
</style>